import React, { useEffect, useState } from "react";
import "./index.scss";
import * as yup from "yup";
import { useFormik } from "formik";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import { useHistory } from "react-router-dom";
import { getCountries, getStates } from "country-state-picker";

// actions
import {
  createUserApi,
  resetData,
} from "../../../../stores/reducers/userSlice";

import {
  CForm,
  CFormGroup,
  CFormText,
  CLabel,
  CInput,
  CCard,
  CCardHeader,
  CCardBody,
  CRow,
  CCol,
  CButton,
  CSelect,
  CInputFile,
} from "@coreui/react";

// components
import LoadingContainer from "../../../../components/clients/common/Loading";

const statusOptions = [
  {
    value: "inactive",
    label: "Inactive",
  },
  {
    value: "active",
    label: "Active",
  },
  {
    value: "blocked",
    label: "Blocked",
  },
  {
    value: "inprogress",
    label: "Inprogress",
  },
  {
    value: "completed",
    label: "Completed",
  },
];

const roleOptions = [
  {
    value: "admin",
    label: "Admin",
  },
  {
    value: "investor",
    label: "Investor",
  },
];

const AddProject = (props) => {
  const history = useHistory();
  const dispatch = useDispatch();
  const { isCreatedUser, error, loading } = useSelector((state) => state.users);
  const [statesData, setStatesData] = useState([]);

  const countriesData = getCountries();

  console.log("countries: ", countriesData);

  const validationSchema = yup.object({
    email: yup
      .string("Enter your email")
      .email("Email invalid")
      .required("Email is required"),
    password: yup
      .string("Enter your password")
      .required("Password is required"),
    phone: yup.string(),
    firstName: yup.string(),
    lastName: yup.string(),
  });

  const formik = useFormik({
    initialValues: {
      email: "",
      password: "",
      phone: "",
      firstName: "",
      lastName: "",
      status: statusOptions[0].value,
      role: roleOptions[1].value,
    },
    validationSchema,
    onSubmit: async (values) => {
      console.log("values: ", values);
      await dispatch(createUserApi(values));
    },
  });

  useEffect(() => {
    return () => {
      dispatch(resetData());
    };
  }, [dispatch]);

  useEffect(() => {
    if (isCreatedUser) {
      toast.success("Created user successfully");
      formik.resetForm();
      dispatch(resetData());
      history.goBack();
    }
    return () => {
      dispatch(resetData());
    };
  }, [isCreatedUser, dispatch, formik, history]);

  useEffect(() => {
    if (error) {
      toast.error("Created user failed");
      dispatch(resetData());
    }
    return () => {
      dispatch(resetData());
    };
  }, [dispatch, error]);

  const handleBack = () => {
    history.goBack();
  };

  const handleChangeCountry = async (event) => {
    console.log(event.target.value);
    const countryCode = event.target.value;
    const _statesData = await getStates(countryCode);
    if (_statesData) {
      setStatesData(_statesData);
    }
  };

  return (
    <div className="addProject">
      <CForm onSubmit={formik.handleSubmit} method="post">
        {loading ? (
          <LoadingContainer />
        ) : (
          <CCard>
            <CCardHeader>Add Project</CCardHeader>
            <CCardBody>
              <CRow>
                <CCol md={12}>
                  <h3 className="title">Main</h3>
                </CCol>
                <CCol md={6}>
                  <CFormGroup>
                    <CLabel>Name</CLabel>
                    <CInput
                      type="text"
                      name="name"
                      // value={formik.values.email}
                      onChange={formik.handleChange}
                    />
                    {formik.errors.name && formik.touched.name && (
                      <CFormText className="help-block textError">
                        {formik.errors.name}
                      </CFormText>
                    )}
                  </CFormGroup>
                </CCol>

                <CCol md={6}>
                  <CFormGroup>
                    <CLabel>Address 1</CLabel>
                    <CInput
                      type="text"
                      name="address"
                      // value={formik.values.password}
                      onChange={formik.handleChange}
                    />
                    {formik.errors.address && formik.touched.address && (
                      <CFormText className="help-block textError">
                        {formik.errors.address}
                      </CFormText>
                    )}
                  </CFormGroup>
                </CCol>

                <CCol md={6}>
                  <CFormGroup>
                    <CLabel>Address 2</CLabel>
                    <CInput
                      type="text"
                      name="address2"
                      // value={formik.values.password}
                      onChange={formik.handleChange}
                    />
                    {formik.errors.address2 && formik.touched.address2 && (
                      <CFormText className="help-block textError">
                        {formik.errors.address2}
                      </CFormText>
                    )}
                  </CFormGroup>
                </CCol>

                <CCol md={6}>
                  <CFormGroup>
                    <CLabel>City</CLabel>
                    <CInput
                      type="text"
                      name="city"
                      // value={formik.values.password}
                      onChange={formik.handleChange}
                    />
                    {formik.errors.city && formik.touched.city && (
                      <CFormText className="help-block textError">
                        {formik.errors.city}
                      </CFormText>
                    )}
                  </CFormGroup>
                </CCol>

                <CCol md={6}>
                  <CFormGroup>
                    <CLabel>Country</CLabel>

                    <CSelect name="country" onChange={handleChangeCountry}>
                      {countriesData &&
                        countriesData.length > 0 &&
                        countriesData.map((country, i) => (
                          <option key={i} value={country.code}>
                            {country.name}
                          </option>
                        ))}
                    </CSelect>
                  </CFormGroup>
                </CCol>

                <CCol md={6}>
                  <CFormGroup>
                    <CLabel>State</CLabel>
                    <CSelect name="country">
                      {statesData &&
                        statesData.length > 0 &&
                        statesData.map((state, i) => (
                          <option key={i} value={state}>
                            {state}
                          </option>
                        ))}
                    </CSelect>
                  </CFormGroup>
                </CCol>

                <CCol md={6}>
                  <CFormGroup>
                    <CLabel>Zipcode</CLabel>
                    <CInput
                      type="text"
                      name="zipcode"
                      value={formik.values.zipcode}
                      onChange={formik.handleChange}
                    />
                  </CFormGroup>
                </CCol>

                <CCol md={6}>
                  <CFormGroup>
                    <CLabel>Duraction</CLabel>
                    <CInput
                      type="text"
                      name="duraction"
                      value={formik.values.duraction}
                      onChange={formik.handleChange}
                    />
                  </CFormGroup>
                </CCol>

                <CCol md={6}>
                  <CFormGroup>
                    <CLabel>Status</CLabel>
                    <CSelect
                      name="status"
                      value={formik.values.status}
                      onChange={formik.handleChange}
                    >
                      {statusOptions.map((el, i) => (
                        <option key={i} value={el.value}>
                          {el.label}
                        </option>
                      ))}
                    </CSelect>
                  </CFormGroup>
                </CCol>

                <CCol md={12}>
                  <h3 className="title">Photos</h3>
                </CCol>

                <CCol md={6}>
                  <CFormGroup>
                    <CLabel>Photo</CLabel>
                    <CInputFile name="picture" multiple={false} />
                  </CFormGroup>
                </CCol>

                <CCol md={6}>
                  <CFormGroup>
                    <CLabel>Gallery</CLabel>
                    <CInputFile name="gallery" multiple={true} />
                  </CFormGroup>
                </CCol>


                <CCol md={12}>
                  <h3 className='title'>Detail</h3>
                </CCol>

                <CCol md={12}>
                  <div className="actions">
                    <CButton color="danger" onClick={handleBack}>
                      Back
                    </CButton>
                    <CButton type="submit" color="success">
                      Add
                    </CButton>
                  </div>
                </CCol>
              </CRow>
            </CCardBody>
          </CCard>
        )}
      </CForm>
    </div>
  );
};

export default AddProject;
